import * as React from "react";
import { Route, Switch } from "react-router"; // react-router v4

const renderMiss = () => <div>Miss</div>;

export const makeRoutes = (App: React.SFC, Profile: React.SFC) => (
  <div>
    {" "}
    {/* your usual react-router v4 routing */}
    <Switch>
      <Route exact={true} path={process.env.REACT_APP_REPO_PATHNAME + "/"}>
        <App />
      </Route>
      <Route
        exact={true}
        path={process.env.REACT_APP_REPO_PATHNAME + "/profile"}
      >
        <Profile />
      </Route>
      <Route render={renderMiss} />
    </Switch>
  </div>
);

export default makeRoutes;



